.checkbox-wrapper-full input[type="checkbox"] {
    display: none;
    visibility: hidden;
  }
  
  .checkbox-wrapper-full .cbx {
    margin: auto;
    -webkit-user-select: none;
    user-select: none;
    cursor: pointer;
  }
  
  .checkbox-wrapper-full .cbx span {
    display: inline-block;
    vertical-align: middle;
    transform: translate3d(0, 0, 0);
  }
  
  .checkbox-wrapper-full .cbx span:first-child {
    position: relative;
    width: 18px;
    height: 18px;
    border-radius: 3px;
    transform: scale(1);
    vertical-align: middle;
    border: 1px solid #9098A9;
    transition: all 0.2s ease;
  }
  
  .checkbox-wrapper-full .cbx span:first-child svg {
    position: absolute;
    top: 3px;
    left: 2px;
    fill: none;
    stroke: #FFFFFF;
    stroke-width: 2;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-dasharray: 16px;
    stroke-dashoffset: 16px;
    transition: all 0.3s ease;
    transition-delay: 0.1s;
    transform: translate3d(0, 0, 0);
  }
  
  .checkbox-wrapper-full .cbx span:first-child:before {
    content: "";
    width: 100%;
    height: 100%;
    background: rgb(109, 171, 108);
    display: block;
    transform: scale(0);
    opacity: 1;
    border-radius: 50%;
  }
  
  .checkbox-wrapper-full .cbx span:last-child {
    padding-left: 8px;
  }
  
  .checkbox-wrapper-full .cbx:hover span:first-child {
    border-color: rgb(109, 171, 108);
  }
  
  .checkbox-wrapper-full .inp-cbx:checked + .cbx span:first-child {
    background: rgb(109, 171, 108);
    border-color: rgb(109, 171, 108);
    animation: wave-46 0.4s ease;
  }
  
  .checkbox-wrapper-full .inp-cbx:checked + .cbx span:first-child svg {
    stroke-dashoffset: 0;
  }
  
  .checkbox-wrapper-full .inp-cbx:checked + .cbx span:first-child:before {
    transform: scale(2.3);
    opacity: 0;
    transition: all 0.6s ease;
  }
  
  @keyframes wave-46 {
    50% {
      transform: scale(0.9);
    }
  }
  




