/* Checkbox Wrapper 37 */
.checkbox-wrapper-Transparent input[type="checkbox"] {
    display: none;
}

.checkbox-wrapper-Transparent .terms-label {
    cursor: pointer;
    display: flex;
    align-items: center;
}

.checkbox-wrapper-Transparent .terms-label .label-text {
    margin-left: 10px;
}

.checkbox-wrapper-Transparent .checkbox-svg {
    width: 30px;
    height: 30px;
}

.checkbox-wrapper-Transparent .checkbox-box {
    fill: #fff;
    stroke: #ff7a00;
    stroke-dasharray: 800;
    stroke-dashoffset: 800;
    transition: stroke-dashoffset 0.6s ease-in;
}

.checkbox-wrapper-Transparent .checkbox-tick {
    stroke: #ff7a00;
    stroke-dasharray: 172;
    stroke-dashoffset: 172;
    transition: stroke-dashoffset 0.6s ease-in;
}

.checkbox-wrapper-Transparent input[type="checkbox"]:checked + .terms-label .checkbox-box,
.checkbox-wrapper-Transparent input[type="checkbox"]:checked + .terms-label .checkbox-tick {
    stroke-dashoffset: 0;
}

