/* Base Theme for Grid */
.ag-theme-dark {
    /* Background color for the entire grid container */
    --ag-background-color: rgba(52, 52, 52, 1) !important;

    /* Default text color for the grid */
    --ag-foreground-color: rgba(235, 235, 235, 0.8) !important;

    /* Header styling */
    /* Background color for the header cells */
    --ag-header-background-color: rgba(52, 52, 52, 1) !important;

    /* Text color in header cells */
    --ag-header-foreground-color: rgba(235, 235, 235, 0.8) !important;

    /* Border colors for the rows and cells */
    /* Default border color for cells */
    --ag-border-color: rgba(235, 235, 235, 0.16) !important;

    /* Row-specific border color */
    --ag-row-border-color: rgba(235, 235, 235, 0.16) !important;
}

/* Floating filter styles */
.ag-theme-dark input {
    /* Background color for input fields */
    background-color: rgba(40, 40, 40, 1) !important;

    /* Text color within input fields */
    color: rgba(235, 235, 235, 0.8) !important;

    /* Border color for input fields */
    border: 1px solid rgba(235, 235, 235, 0.16) !important;
}

.ag-theme-dark .ag-input-field {
    margin: auto;
}

/* Remove border for floating filter inputs to blend with design */
.ag-theme-dark .ag-floating-filter-input {
    border: 0 !important;
}

/* Header cell resizer styling */
.ag-theme-dark .ag-header-cell-resize::after {
    /* Width of the resizer line */
    width: 1px !important;

    /* Height of the resizer line as a percentage of cell height */
    height: 70% !important;

    /* Vertical alignment offset for resizer */
    top: 7px !important;

    /* Color for resizer line */
    background-color: rgba(235, 235, 235, 0.2) !important ;
}

/* Line for floating filter and first column */
.ag-theme-dark .ag-floating-filter::after {
    /* Width of the floating filter line */
    width: 10px !important;

    /* Height as a percentage for consistent placement */
    height: 70% !important;

    /* Color of the floating filter line */
    background-color: rgba(235, 235, 235, 0.2) !important ;
}

/* Selection checkbox color */
.ag-theme-dark .ag-selection-checkbox {
    /* White color for checkboxes for clarity */
    color: #FFFFFF !important;
}

/* Scrollbar styling */
.ag-theme-dark .ag-body-horizontal-scroll-viewport,
.ag-theme-dark .ag-body-viewport {
    /* Background color for scrollbars to match theme */
    background-color: rgba(52, 52, 52, 1) !important;
}

.ag-theme-dark .ag-menu-list, .ag-theme-dark .ag-focus-managed {
    background-color: rgba(52, 52, 52, 1) !important;
}

.ag-theme-dark .ag-menu {
    border: 0px solid rgba(235, 235, 235, 0.16) !important;
}

.ag-theme-dark .ag-menu-option:hover {
    background-color: rgba(40, 40, 40, 1) !important;
}

.ag-theme-dark .ag-menu-option:focus {
    background-color: rgba(40, 40, 40, 1) !important;
}


/* ag grid row selected and hover bg color */
.ag-row-selected::before {
    background: transparent !important;
}

.ag-row-hover::before {
    background: transparent !important;
}

.ag-theme-alpine .ag-row-hover {
    background: rgba(211, 230, 211, .3) !important;
}

.ag-theme-alpine .ag-row-selected {
    background: rgba(138, 188, 137, .3) !important;
}

/* Row color */
.ag-row,
.ag-cell {
    /* background-color: rgba(40, 40, 40, 1) !important;
    color: rgba(235, 235, 235, 0.8) !important; */
}

.ag-theme-dark .ag-row-hover {
    background: rgba(235, 235, 235, 0.05) !important;
}

.ag-theme-dark .ag-row-selected {
    background: rgba(138, 188, 137, 0.08) !important;
}

.hover-over {
background-color: rgba(0, 123, 255, 0.1);
}