@tailwind base;
@tailwind components;
@tailwind utilities;

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none; 
  font-family: inherit;
}

html {
  overflow-y: scroll;
  overflow-x: hidden;
}

.theme-transition {
  animation: fadeTransition 0.3s ease-in-out;
}

html, body {
  height: 100%; 
  margin: 0;
  padding: 0;
  font-family: 'Roboto'; 
  -webkit-font-smoothing: antialiased; 
  -moz-osx-font-smoothing: grayscale;
}

button {
  background: none;
  /* border: none; */
  padding: 0;
  font: inherit;
  cursor: pointer;
}

/* -------------------------------- scroll bar -------------------------------- */
/* Global styles for custom scrollbars and selection */
::selection {
  background-color: rgba(0, 0, 0, 0.1); /* Subtle selection color */
}

::-webkit-scrollbar {
  width: 8px; /* Width of the scrollbar */
}

::-webkit-scrollbar-thumb { 
  background-color: rgba(0, 0, 0, 0.24); /* Color of the scroll thumb */
  border-radius: 4px; /* Rounded corners */
  box-shadow: inset -3px 0 0 0 white; /* Optional shadow for the thumb */
}

::-webkit-scrollbar-thumb:hover {
  background-color: rgba(17, 17, 17, 0.08); /* Color change on hover */
}

::-webkit-scrollbar-track { 
  background-color: rgba(17, 17, 17, 0.08); /* Scroll track color */
  border-radius: 4px; /* Rounded corners */
  margin: 60px 0 10px 0; /* Margins for spacing between track ends */
  box-shadow: inset -3px 0 0 0 white; /* Optional shadow for the track */
}


/* custom scrollBar */
.scrolbar-top-0::-webkit-scrollbar {
  width: 4px; /* Width of the scrollbar */
}

.scrolbar-top-0::-webkit-scrollbar-track { 
  margin: 0px 0px !important; /* Force margin to 0 for this specific class */
  box-shadow: inset 0px 0 0 0 white;
}

.scrolbar-top-0::-webkit-scrollbar-thumb { 
  background-color: rgba(0, 0, 0, 0.24); /* Color of the scroll thumb */
  border-radius: 4px; /* Rounded corners */
  box-shadow: inset 0px 0 0 0 white; /* Optional shadow for the thumb */
}


/* Dark mode styles */
.dark ::selection {
  background-color: rgba(255, 255, 255, 0.2);
}

.dark ::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: inset -3px 0 0 0 rgba(40, 40, 40, 1);
}

.dark ::-webkit-scrollbar-thumb:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.dark ::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: inset -3px 0 0 0 rgba(40, 40, 40, 1);
}



.dark .scrolbar-top-0::-webkit-scrollbar {
  width: 5px; /* Width of the scrollbar */
}

.dark .scrolbar-top-0::selection {
  background-color: rgba(255, 255, 255, 0.2);
}

.dark .scrolbar-top-0::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: inset -2px 0 0 0 rgba(40, 40, 40, 1);
}

.dark .scrolbar-top-0::-webkit-scrollbar-thumb:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.dark .scrolbar-top-0::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: inset -2px 0 0 0 rgba(40, 40, 40, 1);
}

/* --------------------------------             -------------------------------- */


/* change theme animation */
@keyframes fadeTransition {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}