.ag-header-container {
    z-index: 2 !important;
}

.ag-paging-panel {
    display: flex;
    justify-content: flex-start !important; /* Align the pagination to the left */
}

.ag-paging-panel .ag-label {
    display: none !important;  /* Hides page size label */
}

.ag-root-wrapper {
    border-radius: 5px 5px 0 0 !important;
}

.ag-header-cell-label {
    justify-content: center !important;
}

.filter-builder {
    background-color: color-mix(in srgb, #fff, #182230 93%);
    border-radius: 10px;
    color: white;
    font-size: 14px;
    font-weight: 500;
}

.filter-builder-header {
    background-color: color-mix(in srgb, #fff, #182230 97%);
}

.filter-builder-footer {
    background-color: color-mix(in srgb, #fff, #182230 93%);
}

.filter-group {
    position: relative;
    margin-bottom: 15px;
    margin-left: 10px;
}

.first-filter-group-item {

}

.filter-group-item {

}

.filter-group-item-lines::before {
    content: '';
    position: absolute;
    left: -56px;
    top: 10px;
    width: 20px;
    height: 0rem;
    border-bottom: 1px solid rgb(141, 141, 141);
}

.filter-group-First-item-lines::after {
    content: '';
    position: absolute;
    top: 12px;
    left: -24px;
    width: 1px;
    height: 27px;
    border-left: 1px solid rgb(141, 141, 141);
}

.filter-group-item-lines::after {
    content: '';
    position: absolute;
    top: -12px;
    left: -56px;
    width: 1px;
    height: 48px;
    border-left: 1px solid rgb(141, 141, 141);
}

.filter-condition {
    display: flex;
    flex-direction: row;
    justify-content: space-between; 
    align-items: center;
    gap: 10px;
    margin-left: 4rem;   
}

.filter-condition-selections {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.filter-condition-selections-lines::before {
    content: '';
    position: absolute;
    left: 0px;
    width: 20px;
    height: 0rem;
    border-bottom: 1px solid rgb(141, 141, 141);
}

.filter-condition-selections-lines::after {
    content: '';
    position: absolute;
    left: 0px;
    top: -2px;
    width: 1px;
    height: 48px;
    border-left: 1px solid rgb(141, 141, 141);
}

.add-new-group-btn-lines::before {
    content: '';
    position: absolute;
    left: -30px;
    width: 20px;
    height: 0rem;
    border-bottom: 1px solid rgb(141, 141, 141);
}

.add-new-group-btn-lines::after {
    content: '';
    position: absolute;
    left: -30px;
    width: 1px;
    bottom: 10px;
    height: 20px;
    border-left: 1px solid rgb(141, 141, 141);
}

.filter-condition-selections > select {
    height:30px;
    width: 8rem;
    /* color: white */
}

