.filter-condition-btn {
    display: flex;
    flex-direction: row;
    gap: .8rem;
    width: fit-content;
}

.circleBtn {
    width: 1.7rem;
    height: 1.7rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #969696;
    border-radius: 100%;
    color: #969696;
    font-size: 15px;
    font-weight: bolder;
}

.filter-condition-btn > button:hover {
    scale: 1.06;
}

.actions {
    display: flex;
    gap: 10px;
    gap:1rem;
    margin: .5rem 0;
}

.actionsBtn {
    margin-top: 1rem;
    padding: 4px 20px;
    border-radius: 5px;
    background-color: #242431;
}

select, input {
    padding: 5px;
    border-radius: 5px;
    color: #2a2a3b;
}

.custom-grid .ag-header {
    background-color: #fff !important; /* Green background */
