/* dark mode */
/* Dark theme styling for AG Grid */
.ag-theme-dark {
    --ag-background-color: rgba(40, 40, 40, 1);
    --ag-header-background-color: rgba(52, 52, 52, 1);
    --ag-header-border-color: rgba(235, 235, 235, 0.16);
    --ag-floating-filter-input-background-color: rgba(52, 52, 52, 1);
    --ag-font-color: rgba(235, 235, 235, 0.87); /* Text color for headers and cells */
  }
  
  /* Header styling */
  .ag-theme-dark .ag-header {
    background-color: var(--ag-header-background-color);
    color: var(--ag-font-color);
    border-bottom: 1px solid var(--ag-header-border-color);
  }
  
  /* Row styling */
  .ag-theme-dark .ag-row {
    background-color: var(--ag-background-color);
    color: var(--ag-font-color);
  }
  
  /* Floating filter input styling */
  .ag-theme-dark .ag-floating-filter-input {
    background-color: var(--ag-floating-filter-input-background-color);
    color: var(--ag-font-color);
    border: 1px solid var(--ag-header-border-color);
  }
  

  .ag-ltr .ag-cell-focus:not(.ag-cell-range-selected):focus-within {
    border: 0px solid gray !important;
  }
  .hover-over {
    background-color: rgba(0, 123, 255, 0.1)
  }